<!-- -->
<template>
	<div class="alert-shade"> <!-- -->
		<div class='alert-box'>  <!-- -->
			<!--    <button  @click="close" ></button>-->
			<div class="alert-head">
				<span class='head-title'> </span>
				<img @click="close" src="../assets/images/x.png" />
			</div>
			<ul class="alert-body">
				<li>
					<span></span>
					<input type="password" v-model="pw.old_pw" />
				</li>
				<li>
					<span></span>
					<input type="password" v-model="pw.new_pw"/>
				</li>
				<li>
					<span></span>
					<input type="password"  v-model="pw.enter_pw"/>
				</li>
			</ul>
			<div>
				<button class="btn btn-success"  @click="enter"></button>
				<button class="btn btn-info" @click="close"> </button>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data(){ // 
			return {
				pw:{
					old_pw:"",// 
					new_pw:"",// 
					enter_pw:""// 
				}
			}
		},
		methods:{ // 
			close:function(){
				//   == close
				this.$emit("close")
			},
			enter:function(){ // 
				//   1.   2.    3.
				// v-model pw

				//        
				//  
				//    err == 
				//    success==  
				if(this.pw.old_pw!=""&&this.pw.new_pw!=""&&this.pw.enter_pw!=""){
					//  ==  
					if(this.pw.new_pw==this.pw.enter_pw){
						// 
						this.$axios({
							url:"/VueHandler/AdminHandler?action=updatepass",
							method:"post",
							data:{
								userPwd:this.pw.old_pw,
								newPwd:this.pw.new_pw
							}
						}).then((res)=>{ // ajax  
							if(res.data.success){
								alert('');
								// 
								this.pw={
									old_pw:"",// 
									new_pw:"",// 
									enter_pw:""// 
								}
								// 
								this.close();
							}else{
								alert('')
								alert(res.data.err)
							}
						}).catch(function(err){
							alert(err)
						})
					}else{
						alert("")
					}
				}else{
					alert('')
				}

			}
		}
	}
</script>
<style scoped>

	.alert-shade {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		margin: auto;
		z-index: 10002;
		/* background: black; */
		/* opacity: 0.8; */
		background: rgba(000, 000, 000, 0.5);

	}
	.alert-box {
		opacity: 1;
		width:410px;
		height:200px;
		background: #fff;
		border: 5px solid #4f66bb;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 10003;
		margin: auto;
	}



	.alert-head {
		height: 30px;
		background: #4F66B3;
		margin-bottom: 10px
	}

	.head-title {
		color: white;
		line-height: 30px;
		font-size: 14px;
		display: inline-block;
		float: left
	}

	.alert-head img {
		display: inline-block;
		float: right;
		margin-top: 5px;
		margin-right: 5px;
		cursor: pointer;
	}

	ul.alert-body li {
		display: block;
		margin-bottom: 10px
	}

	ul.alert-body li span {
		display: inline-block;
		width: 80px;
		text-align: right;
	}

	ul.alert-body li input {
		height: 18px;
		width: 200px;
	}

	.btn {
		width: 80px;
		height: 30px;
		line-height: 20px;
		border: none;
		color: #fff;
		text-align: center;
		cursor: pointer;
	}

	.btn-info {
		background: #4F66BD;
		float: right;
		margin-right: 60px;
	}

	.btn-info:hover {
		background: #4F66A4;
	}

	.btn-success {
		background: #5cb85c;
		float: left;
		margin-left: 60px;
	}

	.btn-success:hover {
		background: #449d44;
	}
</style>


