<template>
	<div>
		<div>
			<fchead></fchead>
		</div>
		<!--  + -->
		<div class="adminListContent ">
			<!--  -->
			<span style="color:#2880E4">/</span> <span>/</span> <span></span>
			<!--   ===   -->
			<div class="fc_section_wrap">
				<!--    -->
				<ul>
					<li class="admin_top_input">
						<input v-model="searchText" type="text"  placeholder="" />
					</li>
					<li>
						<button class="btn_class" @click="search" style="background: #5cb85c;"></button>
					</li>
					<li class="admin_top_add">
						<button  class="btn_class" @click="addAdmin"  style="background:#4F66B3;" >+ </button>
					</li>
				</ul>
			</div>

			<!--  -->
			<div class="box">
				<div class="box_top">
					<div class="box_left"></div>
					<div class="box_right"></div>
				</div>
				
				<div class="box_bottom">
					<!--  -->
					<!--<h1></h1>-->
					<tablelist  v-bind:tableListData='tableListData'></tablelist>
					<!--   ==              props  --> 
					<!--   props    -->
					<!--   props:["1","2"]-->
					
					<!--    v-bind
						
					-->
					<!--  v-bind:1 =""-->
					
					
					<!--{{tableListData.list}}-->
				</div>
			</div>
		</div>
		<!--   -->
		<div v-show='alertSwitch'>
			<admin-alert></admin-alert>
		</div>
		
	</div>
</template>
<script>
	//  
	// 1.   es6
	// 2.     components{}
	// 3.   
	//    fchead
	import fchead from '@/components/fchead.vue';
	// 
	import tablelist from '@/components/tableList.vue';
	// 
	import adminAlert from '@/components/adminAlert.vue';
	// adminStore.js
	//  data| computed 
	//   tablelist
	// tablelist
	import adminStore from '@/vuex/adminStore.js'
	export default {
		computed:{ // 
			alertSwitch:function(){
				// adminStorestate 
				return adminStore.state.alertSwitch;
			}
		},
		
		components:{fchead,tablelist,adminAlert},
		data(){
			return {
				// 
				tableListData:adminStore.state.tableList,
				// 
				searchText:""
			}
		},
		mounted:function(){ //  
			this.tableListData.getAdminData(); // 		
		},
		methods:{
			addAdmin:function(){ //  			
				// adminStore 
				adminStore.commit("changeAlertSwitch",true);
				// 
				adminStore.commit('setAlertData');
				
			},
			
			
			search:function(){//  
				//      store   
				// 1. v-model 2. ref this.$refs
				// 
				//   v-model
				// 1.  				
				// 2.    search
				// 3.     
				 //    ==    store   
				// 4 . 
				// 5 . store     
//				this.searchText ==>
				adminStore.state.searchText=this.searchText;
				this.tableListData.getAdminData(); // 
				this.searchText="";
				adminStore.state.searchText=this.searchText;
			}
		}
	}
</script>

<style scoped>
	.adminListContent {
		padding-top: 10px;
		padding-left: 10px;
		padding-right: 10px;
		background: #F5F5F5;
		min-width: 1100px;
		height: 100%;
	}
	
	.admin_top_input input {
		width: 250px;
		height: 25px;
		padding-left: 5px;
	}
	
	.fc_section_wrap {
		height: 35px;
		padding: 10px;
		border: 1px solid #C0C0C0;
		background: #FFFFFF;
		margin-top: 15px;
		margin-botton: 15px;
	}
	
	.fc_section_wrap ul {
		margin: 0;
		padding: 0;
	}
	
	.fc_section_wrap ul li {
		display: inline-block;
		margin: 0;
		margin-right: 5px;
		padding: 0;
	}
	
	.admin_top_add {
		float: right;
	}
	
	.btn_class {
		width: 80px;
		height: 32px;
		margin: 0;
		padding: 0;
		border: none;
		cursor: pointer;
		color: #fff;
		margin-right: 10px;
	}
	
	.box {
		width: 100%;
		line-height: 34px;
		background: #fff;
		text-align: left;
		margin-top: 15px;
		border: 1px solid #C0C0C0;
	}
	
	.box_top {
		height: 34px;
		position: relative;
		border-bottom: 1px solid #C0C0C0;
	}
	
	.box_left {
		position: absolute;
		width: 34px;
		height: 34px;
		border-right: 1px solid #C0C0C0;
		background: url(../assets/box.png) no-repeat center;
	}
	
	.box_right {
		padding-left: 40px;
	}
	
	.box_bottom {
		overflow: hidden;
	}

	
</style>


