<template>
	<div>
		<div>
			<fchead></fchead>
			<!---->
		</div>
		<!--  + -->
		<div class="adminListContent ">
			<!--  -->
			<span style="color:#2880E4">/</span> <span>/</span> <span></span>
			<!--   ===   -->
			<div class="fc_section_wrap">
				<!--    -->
				<ul>
					<li class="admin_top_input">
						<input type="text" v-model="searchText" placeholder="" />
					</li>
					<li>
						<button class="btn_class" style="background: #5cb85c;" @click="search()"></button>
					</li>
					<li class="admin_top_add">
						<button @click="alertShow" class="btn_class" style="background:#4F66B3;">+ </button>
					</li>
				</ul>
			</div>

			<!--  -->
			<div class="box">
				<div class="box_top">
					<div class="box_left"></div>
					<div class="box_right"></div>
				</div>
				<div class="box_bottom">
					<table-list v-bind:tableListData="tableListData"></table-list>
				</div>
			</div>

		</div>
		<!---->
		<div v-show="isSwitch">
			<!--  -->
			<video-alert></video-alert>
		</div>
	</div>
</template>
<script>
	/*  fchead*/
	import fchead from "@/components/fchead.vue";
	/*  */
	import videoAlert from "@/components/videoAlert.vue";
	// 
	import tableList from "@/components/tableList.vue";
	// 
	import videoStore from "@/vuex/videoStore.js";

	export default {
		components: {
			fchead,
			videoAlert,
			tableList
		},
		data: function() {
			return {
				tableListData: videoStore.state.tableList, // 
				searchText: ""
			};
		},
		computed: {
			isSwitch: function() {
				return videoStore.state.isSwitch;
			}
		},

		methods: {
			search: function() {
				videoStore.state.searchText = this.searchText;
				videoStore.state.tableList.getVideoData();
				videoStore.state.searchText = "";
				this.searchText = "";
			},
			alertShow: function() {
				// 
				videoStore.commit("changSwitch", true); // 
				videoStore.commit("setInfor"); // 
			}
		},
		mounted: function() {
			// 
			videoStore.state.tableList.getVideoData();
		}
	};
</script>
<style scoped="">
	.adminListContent {
		padding-top: 10px;
		padding-left: 10px;
		padding-right: 10px;
		background: #f5f5f5;
		min-width: 1100px;
		height: 100%;
	}
	
	.admin_top_input input {
		width: 250px;
		height: 25px;
		padding-left: 5px;
	}
	
	.fc_section_wrap {
		height: 35px;
		padding: 10px;
		border: 1px solid #c0c0c0;
		background: #ffffff;
		margin-top: 15px;
		margin-bottom: 15px;
		min-width: 1100px;
	}
	
	.fc_section_wrap ul {
		margin: 0;
		padding: 0;
	}
	
	.fc_section_wrap ul li {
		display: inline-block;
		margin: 0;
		margin-right: 5px;
		padding: 0;
	}
	
	.admin_top_add {
		float: right;
	}
	
	.btn_class {
		width: 80px;
		height: 32px;
		margin: 0;
		padding: 0;
		border: none;
		cursor: pointer;
		color: #fff;
		margin-right: 10px;
	}
	
	.box {
		width: 100%;
		line-height: 34px;
		background: #fff;
		text-align: left;
		margin-top: 15px;
		border: 1px solid #c0c0c0;
	}
	
	.box_top {
		height: 34px;
		position: relative;
		border-bottom: 1px solid #c0c0c0;
	}
	
	.box_left {
		position: absolute;
		width: 34px;
		height: 34px;
		border-right: 1px solid #c0c0c0;
		background: url(../assets/box.png) no-repeat center;
	}
	
	.box_right {
		padding-left: 40px;
	}
	
	.box_bottom {
		overflow: hidden;
	}
</style>


