<template>
	<div class="main">
		<div>
			<fchead></fchead>
		</div>



		<div class="index_body">
			<!--  -->
			<div class="quick_links">
				<div class="quicklinks_one">
					<h1></h1>
					<h2>Course List</h2>
					<div class="icon_wrap"><i class="fa fa-th-large"></i></div>
