					<h2>Course Add</h2>
					<div class="icon_wrap"><i class="fa fa-plus"></i></div>
					<p></p>
				</div>
				<div class="quicklinks_one mouse_on">
					<h1></h1>
					<h2>Course Up</h2>
					<div class="icon_wrap"><i class="fa fa-arrow-up"></i></div>
