				</div>
				<div class="quicklinks_one">
					<h1></h1>
					<h2>Video Manage</h2>
					<div class="icon_wrap"><i class="fa fa-youtube-play"></i></div>
					<p></p>
				</div>
				<div class="quicklinks_one mouse_on">
					<h1></h1>
					<h2>User Manage</h2>
					<div class="icon_wrap"><i class="fa fa-user"></i></div>
					<p></p>
				</div>
				<div class="quicklinks_one mouse_on">
					<h1></h1>
					<h2>Student Manage</h2>
					<div class="icon_wrap"><i class="fa fa-file"></i></div>
					<p></p>
				</div>
			</div>

			<!--  -->
			<!-- -->
			<div class="charts_wrap">
				<!---->
				<div style="background:#fff; padding-top:20px; height:340px;"  >

					<!---->
					<div style="width:400px; height:370px;" ref='echarts01'></div>

				</div>

				<div style="background:#fff; padding-top:20px; height:340px;">

					<div style="width:530px; height:370px;" ref='echarts02'></div>

				</div>

				<div style="background:#fff; padding-top:20px; height:340px;">

					<div style="width:580px; height:370px;" ref='echarts03'></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	// 
	// 
	//    fcHead ==> fc-head
	import fchead from '@/components/fchead.vue';
	//  echarts 
	import  echarts from 'echarts';

		//    echarts
		export default {
			components:{fchead},
			mounted:function(){
			// 1.  echarts     
			// npm install echarts --save
			// 2. 
			//     1)   import  echarts from 'echarts'

			//     2)  echarts  echarts.init('dom')
			//             ref=""    this.$refs.
			//     3)   options
			//              ==   ==   ctrl + f
 			//     4 echarts.setOption({})
			//  echarts      echarts.init()
//			var echarts01 =echarts.init(this.$refs.echarts01);
//			//   
//			var option01 = {
//				title:{ // 
//					text:"",
//					x:"center",
//					textStyle:{
//						color:"red"
//					}
//				},
//		    tooltip : { // 
//		        formatter: "{a} <br/>{b} : {c}%"
//		    },
//		    toolbox: { // 
//		        show : true,
////		        feature : {
////		            mark : {show: true},
////		            restore : {show: true},
////		            saveAsImage : {show: true}
////		        }
//		    },
//		    series : [ //
//		        {
//		            name:'',
//		            type:'gauge',
//		            detail : {formatter:'{value}%'},
//		            data:[{value: 50, name: ''}]
//		        }
//		    ]
//		};
//			//      echarts .setOption(option)
//			echarts01.setOption(option01);
//			//   npm run dev
//			// 



			// ref      this.$refs.xx  
			var echarts01 = echarts.init(this.$refs.echarts01);

			//   option
			var option01 = {
				title: { ////
					text: '',
					subtext: '  ', ////
					x: 'center', // //x   y
					textStyle: {
						color: '#008ACD'
					}
				},

				tooltip: { // 
					formatter: "{a} <br/>{b} : {c}%"
				},
				toolbox: {
					show: true,
					feature: {
						mark: {
							show: true
						},
						restore: {
							show: true
						},
						saveAsImage: {
							show: true
						}
					}
				},
				series: [{ // 
					name: '',
					type: 'gauge',
					splitNumber: 10, // 5
					axisLine: { // 
						lineStyle: { // lineStyle
							color: [
							[0.2, '#228b22'],
							[0.8, '#48b'],
							[1, '#ff4500']
							],
							width: 8
						}
					},
					axisTick: { // 
						splitNumber: 10, // split
						length: 12, // length
						lineStyle: { // lineStyle
							color: 'auto'
						}
					},
					axisLabel: { // axis.axisLabel
						textStyle: { // TEXTSTYLE
							color: 'auto'
						}
					},
					splitLine: { // 
						show: true, // show
						length: 30, // length
						lineStyle: { // lineStylelineStyle
							color: 'auto'
						}
					},
					pointer: {
						width: 5
					},
					title: {
						show: true,
						offsetCenter: [0, '-40%'], // x, ypx
						textStyle: { // TEXTSTYLE
							fontWeight: 'bolder'
						}
					},
					detail: {
						formatter: '{value}%',
						textStyle: { // TEXTSTYLE
							color: 'auto',
							fontWeight: 'bolder'
						}
					},
					data: [{ // 
						value: 60,
						name: ''
					}]
				}]
			};
			// setOption ==  options
			echarts01.setOption(option01)

			var echarts02 = echarts.init(this.$refs.echarts02);
			var option02 = {
				title : {
					text: '',
					subtext: '',
					x:'center'
				},
				color: ['#B6A2DE', '#57D2D3'],
				tooltip : {
					trigger: 'item',
					formatter: "{a} <br/>{b} : {c} ({d}%)"
				},
				calculable : true,
				series : [
				{
					name:'',
					type:'pie',
					center: ['50%', '50%'],
					data:[
					{value:335, name:''},
					{value:310, name:''},

					]
				}
				]
			};

			echarts02.setOption(option02)

			var echarts03 = echarts.init(this.$refs.echarts03);
			var option03 ={
				title: {
					x: 'center',
					text: '',
					subtext: ' '
					,
					textStyle: {
						color: '#008ACD'
					}
        },// 
        tooltip: {
        	trigger: 'item'
        },// 
        calculable: true, //
        grid: {
        	borderWidth: 0,
        	y: 80,
        	y2: 60
        },// 
        xAxis: [
        {
        	type: 'category',
        	show: false,
        	data: ['', '', '']
        }
        ],// x
        yAxis: [
        {
        	type: 'value',
        	show: false
        }
        ], // y
        series: [//
        {
        	name: '',
        	type: 'bar',
        	itemStyle: {
        		normal: {
        			color: function (params) {
        				var colorList = [
        				'#C1232B', '#B5C334', '#F0805A'
        				];
        				return colorList[params.dataIndex]
        			},

        			barBorderRadius: 5,
        			label: {
        				show: true,
        				position: 'top',
        				formatter: '{b}\n{c}'
        			}
        		}
        	},
        	data: [12, 21, 10]
        }
        ]
    };

    echarts03.setOption(option03)


}
}
</script>
<style scoped>
	.index_body {
		background: url(../assets/images/index_body_bg.jpg);
		padding-top: 65px;
		color: #fff;
	}

	.quick_links {
		overflow: hidden;
	}

	.quicklinks_one {
		width: 12%;
		height: 202px;
		background: url(../assets/images/white_opacity_bg.png);
		position: relative;
		text-align: center;
		cursor: pointer;
		float: left;
		margin-left: 4%;
		margin-bottom: 54px;
	}

	.quicklinks_one h1 {
		font-size: 24px;
		font-family: "", "";
		padding-top: 38px;
	}

	.quicklinks_one h2 {
		font-size: 12px;
		font-family: Arial, Helvetica, sans-serif;
		padding-top: 12px;
		font-weight: normal;
	}

	.quicklinks_one p {
		font-size: 12px;
		padding: 5px 22px 0;
		display: none;
		line-height: 21px;
	}

	.quicklinks_one .icon_wrap {
		position: absolute;
		top: 130px;
		left: 0;
		width: 100%;
	}

	.quicklinks_one i {
		font-size: 40px;
	}

	.quicklinks_one i {
		border: none;
		height: 20px;
	}

	.quicklinks_one .icon-plus {
		border: 4px solid #fff;
		border-radius: 2px;
	}

	.quicklinks_one .icon::before {
		position: absolute;
		left: -25px;
		top: -10px;
	}

	.quicklinks_one .icon-plus:before {
		width: 20px;
		border: 4px solid #fff;
		border-radius: 2px;
		left: -13px;
		top: 5px;
	}

	.quicklinks_one:hover {
		background-color: #27a9e3;
		transition: all 0.5s ease 0s;
	}

	.quicklinks_one:hover .icon_wrap {
		top: 22px;
		transition: all 0.5s ease 0s;
	}

	.quicklinks_one:hover h1 {
		padding-top: 78px;
		transition: all 0.5s ease 0s;
	}

	.quicklinks_one:hover h2 {
		display: none;
		transition: all 0.5s ease 0s;
	}

	.quicklinks_one:hover p {
		display: block;
		transition: all 0.5s ease 0s;
	}

	.quicklinks_one.disabled {
		opacity: 0.6;
		cursor: not-allowed;
	}
	/**/

	.charts_wrap {
		margin-left: 4%;
		margin-right: 4%;
		background: url(../assets/images/white_opacity_bg.png);
		padding-top: 50px;
		overflow: hidden;
	}

	.charts_wrap>div {
		float: left;
		margin-left: 4%;
		overflow: hidden;
		margin-bottom: 50px;
	}

</style>


