// vuex  == adminStore.js ==  

//  
// 1. 
// 2. 
// 3. 

// vuex   
//  1.  vue
//  2. vuex
//  3. vuex
//  4.  store   state mutations
//  5.  store 

//  ==   == axios == axios 
import axios from "axios";

import Vue from 'vue'; // Vue.prototype.$axios()
import Vuex from 'vuex';
Vue.use(Vuex);
//       
var getAdminData = function(num) { // num ==> 
    if (num) {
        state.pageStart = num; //  state pageStart
        //                      =    
        state.tableList.pageStart = state.pageStart;
    }
    axios({
        method: "get",
        url: "/VueHandler/AdminHandler?action=show",
        params: {
            searchText: state.searchText, // 
            pageStart: state.pageStart // 
        }
    }).then(function(res) {
        // 
        state.tableList.list = [];
        // 
        if (res.data.err) { // 
            alert(res.data.err)
        } else { // 
            //        /  =   
            // Math.ceil() ==>   1.1 ==2

            state.maxAdminCount = Math.ceil(res.data.data.count / state.pageSize);


            // res.data==>   {success:"",data:{} }
            //			res.data.data ==  
            //  statetableListlist
            for (var i = 0; i < res.data.data.pageSize; i++) {
                if (res.data.data.list[i]) { // 
                    var obj = {
                            // type
                            userName: res.data.data.list[i].userName, // 
                            turename: res.data.data.list[i].turename,
                            power: res.data.data.list[i].power,
                            powerCode: res.data.data.list[i].powerCode,
                            phone: res.data.data.list[i].phone,
                            updateAt: res.data.data.list[i].upDateAt,
                            tokenId: res.data.data.list[i].tokenId, //ID
                            // 
                            edit: true, // 
                            delete: true //   
                        }
                        //    
                    state.tableList.list.push(obj)

                } else { // 
                    state.tableList.list.push({})
                }
            }
        }
    }).catch(function(err) {
        alert(err)
    })
}

var state = { // 
    // 
    alertData: {
        userName: "", // 
        tureName: "", // 
        phone: "", // 
        powerCode: "", // 
        tokenId: "" //  id
    },

    alertSwitch: false, // 


    // 
    // 
    searchText: "", // 
    pageStart: 1, // 
    // 
    maxAdminCount: 0, // 
    minAdminCount: 1, // 
    pageSize: 3, // 

    tableList: { // 
        // 
        pageStart: 1, // 1
        //  
        // 1.    
        // 2. mutations   ++
        // 3. mutations    --

        // //  == admin.vue
        getAdminData: getAdminData, // 
        th_list: [ // 
            //{value:"",width:"",type:""}
            { value: "", width: "10%", type: "userName" },
            { value: "", width: "10%", type: "turename" },
            { value: "", width: "20%", type: "power" },
            { value: "", width: "20%", type: "phone" },
            { value: "", width: "20%", type: "updateAt" },
            { value: "", width: "20%" }
        ],
        list: [], // 
        btn_list: { // 
            // 
            update: function(data) { //   
                //   == changeAlertSwitch
                Vue.prototype.$adminStore.commit('changeAlertSwitch', true)

                //                 data= 
                Vue.prototype.$adminStore.commit('setAlertData', data)
            },
            // 
            delete: function(data) { //   
                //  data ==> 
                //  ==>  tokenId
                axios({
                    url: "/VueHandler/AdminHandler?action=delete",
                    method: "get",
                    params: {
                        tokenId: data.tokenId // id ==> tokenId	
                    }
                }).then(function(res) {
                    if (res.data.success) { // 
                        alert('');
                        getAdminData();
                    } else {
                        alert('');
                        alert(res.data.err)
                    }
                }).catch(function(err) {
                    alert(err)
                })
            },
            // 
            firstClick: function() { // 
                //				getAdminData() //      : 
                //				getAdminData(1);
                getAdminData(Vue.prototype.$adminStore.state.minAdminCount)
            },
            lastClick: function() { // 
                //getAdminData() //      : 
                //				state.maxAdminCount
                //Vue.prototype.$axios ==> ajax
                //Vue.prototype.$adminStore ==> 

                // 1. adminStore  vue
                // 2. Vue.prototype.$adminStore.state.maxAdminCount 
                getAdminData(Vue.prototype.$adminStore.state.maxAdminCount);
            },
            nextClick: function() { //    ++
                // 1 . mutations addPageStart 
                // 2. 
                Vue.prototype.$adminStore.commit('addPageStart')
                    // commit()    
                    //                 
                getAdminData();
            },
            prevClick: function() { //    --				
                // 1.  mutations subPageStart
                // 2. 
                Vue.prototype.$adminStore.commit('subPageStart');
                getAdminData();
            }


        }
    }
};
var mutations = { //  
    setAlertData: function(state, data) {
        //   data == 
        //   data == 
        if (data) {
            // ==  == 
            state.alertData.userName = data.userName;
            state.alertData.turename = data.turename;
            state.alertData.phone = data.phone;
            state.alertData.powerCode = data.powerCode;
            state.alertData.tokenId = data.tokenId;
        } else {
            //  == ==  ==    ""
            state.alertData.userName = "";
            state.alertData.turename = "";
            state.alertData.phone = "";
            state.alertData.powerCode = "";
            state.alertData.tokenId = "";
        }


    },
    changeAlertSwitch: function(state, data) { //  
        // data    ==   == 
        //  true ==      
        //  false ==     
        state.alertSwitch = data;
    },
    addPageStart: function(state) { // ++
        // state ==> vuex state   storestate
        // data ==     // 									
        if (state.pageStart < state.maxAdminCount) {
            state.pageStart++;
            //                         // 
            state.tableList.pageStart = state.pageStart;
        } else {
            alert('')
        }
    },
    subPageStart: function(state) { // --
        //  
        if (state.pageStart > state.minAdminCount) {
            state.pageStart--;
            state.tableList.pageStart = state.pageStart;
        } else {
            alert('')
        }
    }
};
export default new Vuex.Store({
    state,
    mutations
})


