// // studentStore.js
/*
 * :  th_list  list  btn_list  
 * 
 * 
 *  
 * */

import axios from "axios";
import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
//   
var getStudentData = function(num) { // num ==> 
    if (num) {
        state.pageStart = num; //  state pageStart
        state.tableList.pageStart = state.pageStart; // 
    }
    // 
    axios({
        url: "/VueHandler/AdminHandler?action=usershow",
        method: "post",
        data: { // 
            userName: state.searchData.userName, // 
            email: state.searchData.email, // 
            phone: state.searchData.phone, // 
            pageStart: state.pageStart // 
        }
    }).then(function(res) { //  
        // 
        state.tableList.list = [];
        // 1. 
        // 2.      
        // 			     
        //            for
        //             list
        if (res.data.err) {
            alert(res.data.err)
        } else {
            // 
            state.maxStudentCount = Math.ceil(res.data.data.count / state.pageSize);

            for (var i = 0; i < state.pageSize; i++) { // for 
                if (res.data.data.list[i]) { // 
                    var obj = {
                        // key   type
                        userName: res.data.data.list[i].userName,
                        email: res.data.data.list[i].email,
                        phone: res.data.data.list[i].phone,
                        createAt: res.data.data.list[i].createAt,
                        status: res.data.data.list[i].isstate ?  '<span style="color:red"></span>':"",
                        isstate: res.data.data.list[i].isstate,
                        tokenId: res.data.data.list[i].tokenId,
                        // 
                        freeze: true, // 
                        //		          edit:false
                    }
                    state.tableList.list.push(obj)
                } else {
                    state.tableList.list.push({})
                }
            }
        }
    }).catch(function(err) {
        alert(err)
    })
}
var state = { // 
    pageStart: 1, //
    pageSize: 6, //
    maxStudentCount: 0, //
    minStudentCount: 1, //
    searchData: { // 
        userName: "",
        email: "",
        phone: ""
    },

    tableList: { // 
        // 
        pageStart: 1,
        getStudentData: getStudentData, // 
        th_list: [ // 
            {
                value: "",
                type: 'userName',
                width: "10%"
            },
            {
                value: "",
                type: 'email',
                width: "20%"
            },
            {
                value: "",
                type: 'phone',
                width: "15%"
            },
            {
                value: "",
                type: 'createAt',
                width: "25%"
            },
            {
                value: "",
                type: 'status',
                width: "10%"
            },
            {
                value: "",
                width: "10%"
            }
        ],
        list: [], // 
        btn_list: { // 
            changeState: function(data) { //  
                // data  
                axios({
                    url: "/VueHandler/AdminHandler?action=lockuser",
                    method: "get",
                    params: {
                        tokenId: data.tokenId // id
                    }
                }).then(function(res) {
                    // 
                    if (res.data.success) {
                        alert('');
                        // 
                        getStudentData();
                    } else {
                        alert(res.data.err);
                    }
                }).catch(function(err) {
                    alert(err)
                })
            },
            // 
            firstClick: function() { // 
                getStudentData(Vue.prototype.$studentStore.state.minStudentCount);
                // vue == main.jsvue
            },
            lastClick: function() { // 	
                getStudentData(Vue.prototype.$studentStore.state.maxStudentCount);
            },
            nextClick: function() { //   
                Vue.prototype.$studentStore.commit('addPageStart');
                getStudentData();
            },
            prevClick: function() { //   
                Vue.prototype.$studentStore.commit('subPrevClick');
                getStudentData();
            }
        }
    }
};
var mutations = {
    addPageStart: function(state) {
        if (state.pageStart < state.maxStudentCount) {
            state.pageStart++;
            state.tableList.pageStart = state.pageStart;
        } else {
            alert('')
        }
    },
    subPrevClick: function(state) {
        if (state.pageStart > 1) {
            state.pageStart--;
            state.tableList.pageStart = state.pageStart;
        } else {
            alert('')
        }
    },
};
export default new Vuex.Store({
    state,
    mutations
})


