//store.js store
//    1vue
//    2 vuex
//    3  vuex    	Vue.use(Vuex)

//    4)  store state mutations **
//    5 store **

//   
import Vue from 'vue';// vue
import Vuex from 'vuex';// vuex
// vuex 
Vue.use(Vuex);
//  state  mutations 
var state={ // 
	num:0,
	text : 'vuex'
};
var mutations={
	add:function(state,data){
		state.num++;
	},
	jian:function(state,data){
		state.num--;
	}
};
// 
export default new Vuex.Store({
	state,
	mutations
})



