<template>
  <div class="alert-shade">
    <!---->
    <div class="alert-box">
      <!-- -->
      <div class="alert-head">
        <span class='head-title'>{{isDisabled()?"":""}}</span>
        <img @click="close" src="../assets/images/x.png" />
      </div>
      <ul class="alert-body">
        <li>
         <span></span>
         <input type="text" v-bind:disabled="isDisabled()" v-model="alertData.userName" />
       </li>
       <li>
         <span></span>
         <input type="text" v-model="alertData.turename" />
       </li>
       <li>
         <span></span>
         <input type="text" v-model="alertData.phone" />
       </li>
       <li>
         <span></span>
         <select v-model="alertData.powerCode">
          <!--  v-for    v-bind-->
          <!--  { power:"",powerCode:""}-->
          <option v-for='power in powerData' v-bind:value="power.powerCode">
           {{power.power}}
         </option>

						<!--<option value="1"></option>
						<option value="2"></option>-->
					</select>
				</li>
			</ul>
			<div>
				<button class="btn btn-success" @click="enter"></button>
				<button class="btn btn-info" @click="close"> </button>
			</div>
		</div>
	</div>
</template>
<script>
//  adminStore      
import adminStore from "../vuex/adminStore";
export default {
  computed: {
    // 
    alertData: function() {
      return adminStore.state.alertData;
    }
  },
  methods: {
    // 
    isDisabled: function() {
      // disabled
      return this.alertData.tokenId ? true : false;
    },
    close: function() {
      //  changeAlertSwitch
      adminStore.commit("changeAlertSwitch", false);
    },
    enter: function() {
      // 
      // 
      var url = ""; // 
      var data = {
        // 
        turename: this.alertData.turename,
        phone: this.alertData.phone,
        powerCode: this.alertData.powerCode
      };
      //    
      if (this.alertData.tokenId) {
        //  == 
        url = "/VueHandler/AdminHandler?action=update"; // 
        data.tokenId = this.alertData.tokenId;
      } else {
        //  ==> 
        url = "/VueHandler/AdminLoginAndRegHandler?action=add"; // 
        data.userName = this.alertData.userName; // 
        data.password = "123456"; //  123456
      }
      //  
      this.$axios({
        url: url,
        data: data,
        method: "post"
      })
      .then(res => {
        if (res.data.success) {
            // 
            alert("");
            // 
            this.close();
            // 
            adminStore.state.tableList.getAdminData();
          } else {
            alert(res.data.err);
          }
        })
      .catch(err => {
        alert(err);
      });
    }
  },
  data() {
    // 
    return {
      powerData: [], // 
    };
  },
  mounted: function() {
    //  
    // this.powerData ==  
    this.$axios({
      url: "/VueHandler/CourseHandler?action=getpower",
      method: "get"
    })
    .then(res => {
        // ajax  
        if (res.data.success) {
          for (var i = 0; i < res.data.data.length; i++) {
            this.powerData.push({
              power: res.data.data[i].power, // 
              powerCode: res.data.data[i].powerCode // 
            });
          }
        } else {
          alert(res.data.err);
        }
      })
    .catch(function(err) {
      alert(err);
    });
  }
};
</script>
<style scoped>
  .alert-shade {
    position: fixed;
    top: 20px;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    z-index: 10002;
    /* background: black; */
    /* opacity: 0.8; */
    background: rgba(000, 000, 000, 0.5);

  }
  .alert-box {
    opacity: 1;
    width: 410px;
    height: 250px;
    background: #fff;
    border: 5px solid #4f66bb;


    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10003;
    margin: auto;
  }
  .alert-head {
    height: 30px;
    background: #4f66b3;
    margin-bottom: 10px;
  }

  .head-title {
    color: white;
    line-height: 30px;
    font-size: 14px;
    display: inline-block;
    float: left;
  }

  .alert-head img {
    display: inline-block;
    float: right;
    margin-top: 5px;
    margin-right: 5px;
    cursor: pointer;
  }

  ul.alert-body li {
    display: block;
    margin-bottom: 10px;
  }

  ul.alert-body li span {
    display: inline-block;
    width: 80px;
    text-align: right;
  }

  ul.alert-body li input {
    height: 18px;
    width: 200px;
  }

  ul.alert-body li select {
    width: 204px;
    height: 24px;
  }

  .btn {
    width: 80px;
    height: 30px;
    line-height: 20px;
    border: none;
    color: #fff;
    text-align: center;
    cursor: pointer;
  }

  .btn-info {
    background: #4f66bd;
    float: right;
    margin-right: 60px;
  }

  .btn-info:hover {
    background: #4f66a4;
  }

  .btn-success {
    background: #5cb85c;
    float: left;
    margin-left: 60px;
  }

  .btn-success:hover {
    background: #449d44;
  }
</style>


