import Vue from 'vue';
import Vuex from 'vuex';
import axios from "axios";
Vue.use(Vuex);

//  
(function() {
    axios({
            method: "get",
            url: "/VueHandler/CourseHandler?action=getcategory"
        })
        .then(res => {
            if (res.data.success) {
                state.catalogList = res.data.data;
            } else {
                alert(res.data.err);
            }
        })
        .catch(err => {
            alert(err);
        });
})();

// 
var getCategory = function(sysId) {
    for (let i = 0, len = state.catalogList.length; i < len; i++) {
        if (state.catalogList[i].sysId === parseInt(sysId)) {
            return state.catalogList[i].title;
        }
    }
    return '';
}

// 
var getCourseData = function(num) {
    if (num) {
        state.pageStart = num;
        state.tableList.pageStart = state.pageStart;
    }
    axios({
        method: "post",
        url: "/VueHandler/CourseHandler?action=show",
        data: {
            searchText: state.searchText, //  &  & 
            CategoryOne: state.categoryData.one, // id
            CategoryTwo: state.categoryData.two, // id
            CategoryThree: state.categoryData.three, // id
            pageStart: state.pageStart // 

        }
    }).then(function(res) {
        state.tableList.list = [];
        // 
        if (res.data.err) { // 
            alert(res.data.err)
        } else { // 
            state.maxCourseCount = Math.ceil(res.data.data.count / state.pageSize);
            // 

            for (var i = 0; i < res.data.data.pageSize; i++) {
                if (res.data.data.list[i]) { // 
                    var obj = {
                        // type
                        Cname: res.data.data.list[i].Cname, // 
                        Cnumber: res.data.data.list[i].Cnumber,
                        onlineUser: res.data.data.list[i].onlineUser,
                        category: getCategory(res.data.data.list[i].CategoryThree),

                        // CategoryOne: res.data.data.list[i].CategoryOne,
                        // CategoryTwo: res.data.data.list[i].CategoryTwo,
                        // CategoryThree: res.data.data.list[i].CategoryThree,

                        createAt: res.data.data.list[i].createAt,
                        isState: res.data.data.list[i].isState === 1 ? "<span ></span>" : res.data.data.list[i].isState === 2 ? "<span ></span>" : "<span style='color:red;'></span>",
                        stateNum: res.data.data.list[i].isState,
                        // stickText:!response.data.data.list[i].isTop?"":"",
                        ID: res.data.data.list[i].ID, // ID
                        isTop: res.data.data.list[i].isTop,
                        // 
                        frame: true, // 
                        stick: true, //  
                        edit: true, // 
                        delete: true, // 
                    }
                    state.tableList.list.push(obj)
                } else {
                    state.tableList.list.push({})
                }
            }
        }
    }).catch(function(err) {
        alert(err)
    })
}

var state = { // 

    catalogList: [], //
    // 
    // 
    searchText: "",
    categoryData: {
        one: "",
        two: "",
        three: ""
    },
    pageStart: 1, // 
    // 
    maxCourseCount: 0, // 
    minCourseCount: 1, // 
    pageSize: 3, // 

    tableList: { // 
        pageStart: 1, // 
        getCourseData: getCourseData, // 
        th_list: [ // 
            { value: "", type: 'Cname', width: "12%" },
            { value: "", type: 'Cnumber', width: "12%" },
            { value: "", type: 'onlineUser', width: "12%" },
            { value: "", type: 'category', width: "12%" },
            { value: "", type: 'createAt', width: "20%" },
            { value: "", type: 'isState', width: "12%" },
            { value: "", width: "20%" }
        ],
        list: [], // 
        btn_list: { // 

            setFrame: function(data) { // 
                axios({
                    url: "/VueHandler/CourseHandler?action=state",
                    method: "post",
                    data: {
                        ID: data.ID
                    }
                }).then((res) => {
                    if (res.data.success) {
                        getCourseData();
                    } else {
                        alert(res.data.err)
                    }
                }).catch((err) => {
                    alert(err)
                })
            },

            top: function(data) { // 
                axios({
                    url: "/VueHandler/CourseHandler?action=top",
                    method: "post",
                    data: {
                        ID: data.ID
                    }
                }).then((res) => {
                    if (res.data.success) {
                        getCourseData();
                    } else {
                        alert(res.data.err)
                    }
                }).catch((err) => {
                    alert(err)
                })
            },

            // 
            update: function(data) { //
                window.location.href = "/courseEdit/" + data.ID
            },
            // 
            delete: function(data) { //
                axios({
                    url: "/VueHandler/CourseHandler?action=delete",
                    method: "post",
                    data: {
                        ID: data.ID // id ==> tokenId
                    }
                }).then(function(res) {
                    if (res.data.success) { // 
                        alert('');
                        getCourseData();
                    } else {
                        alert(res.data.err)
                    }
                }).catch(function(err) {
                    alert(err)
                })
            },
            // 
            firstClick: function() { // 
                getCourseData(Vue.prototype.$courseStore.state.minCourseCount)
            },
            lastClick: function() { // 
                getCourseData(Vue.prototype.$courseStore.state.maxCourseCount);
            },
            nextClick: function() { //    ++
               Vue.prototype.$courseStore.commit('addPageStart')
                getCourseData();
            },
            prevClick: function() {
               Vue.prototype.$courseStore.commit('subPageStart');
                getCourseData();
            }


        }
    }
};
var mutations = {
    addPageStart: function(state) { // ++
    	
    	console.log(state.pageStart)
        if (state.pageStart < state.maxCourseCount) {
            state.pageStart++;

            state.tableList.pageStart = state.pageStart;
        } else {
            alert('')
        }
    },
    subPageStart: function(state) { // --
        if (state.pageStart > state.minCourseCount) {
            state.pageStart--;
            state.tableList.pageStart = state.pageStart;
        } else {
            alert('')
        }
    },
    setCategory: function(state, data) { // 
        state.categoryData = { // 
            one: data.categoryOne,
            two: data.categoryTwo,
            three: data.categoryThree
        }
    }
};
export default new Vuex.Store({
    state,
    mutations
})


