import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
Vue.use(Vuex);
// 
const setListData = function(listName, data, dirId) {
	// 
	state.courseContent[listName].list = [];
	switch(listName) {
		case "dirTableList": // 
			for(var i = 0; i < data.length; i++) {
				if(data[i]) { // 
					var obj = {
						// type
						CDName: data[i].CDName, // 
						CourseNumber: data[i].CourseNumber,
						ID: data[i].ID, //ID
						// 
						edit: true, // 
						delete: true, //  
						isClick: false, //  d
						click: true
					}
					//    
					state.courseContent[listName].list.push(obj)
				} else { // 
					state.courseContent[listName].list.push(obj)
				}
			}
			break;
		case "videoTableList": // 
			for(var i = 0; i < data.length; i++) {
				if(data[i]) {
					var obj = {
						Vname: data[i].Vname,
						Vtime: data[i].Vtime,
						ID: data[i].ID, // ID
						Did: dirId, // id

						// 
						delete: true,
					}
					//    
					state.courseContent[listName].list.push(obj)
				} else { // 
					state.courseContent[listName].list.push({})
				}
			}
			break;
	}
}

//  
const getCourseInfoData = function(ID) {
	axios({
		url: "/VueHandler/CourseHandler?action=getcourse",
		method: "get",
		params: {
			ID: ID // ID
		}
	}).then((res) => {
		if(res.data.success) {
			//  
			let courselist = res.data.data.courselist;
			//  
			let dirlist = res.data.data.dirlist;
			//  
			state.courseInfo.ID = courselist.ID;
			state.courseInfo.Cname = courselist.Cname;
			state.courseInfo.Cdescribe = courselist.Cdescribe;
			state.courseInfo.Cprice = courselist.Cprice;
			state.courseInfo.Cpic = courselist.Cpic;
			state.courseInfo.Cdetails = courselist.Cdetails;
			// 
			state.courseInfo.category.one = courselist.CategoryOne || "";
			state.courseInfo.category.two = courselist.CategoryTwo || "";
			state.courseInfo.category.three = courselist.CategoryThree || "";
			// 
			setListData('dirTableList', dirlist);
		} else {
			alert(res.data.err)
		}
	}).catch((err) => {
		alert(err)
	})
}
const state = {
	//  
	addCourseSwitch: true,

	// 
	// 
	dirAlertData: {
		CDName: "",
		ID: "",
		CDid: ""
	},
	selectVideAlertData: {
		ID: "" // ID
	},

	//  
	btnList: {
		addCoutse: function() {
			axios({
				url: "/VueHandler/CourseHandler?action=add",
				method: "post",
				data: {
					Cname: state.courseInfo.Cname, // 
					Cdescribe: state.courseInfo.Cdescribe, // 
					Cprice: state.courseInfo.Cprice, // 
					CategoryOne: state.courseInfo.newCategory.one, // 
					CategoryTwo: state.courseInfo.newCategory.two, // 
					CategoryThree: state.courseInfo.newCategory.three, // 
					Cpic: state.courseInfo.Cpic, // 
				}
			}).then((res) => {
				if(res.data.success) {
					console.log(res)
					window.location.hash = "/courseEdit/" + res.data.ID
					Vue.prototype.$courseEditStore.commit('changeAddCourseSwitch', false);
					getCourseInfoData(res.data.ID);
				} else {
					alert(res.data.err)
				}
			}).catch((err) => {
				alert(err)
			})
		},
		saveCoutse: function() { //             
			axios({
				url: "/VueHandler/CourseHandler?action=update",
				method: "post",
				data: {
					Cname: state.courseInfo.Cname, // 
					Cdescribe: state.courseInfo.Cdescribe, // 
					Cprice: state.courseInfo.Cprice, // 
					CategoryOne: state.courseInfo.newCategory.one, // 
					CategoryTwo: state.courseInfo.newCategory.two, // 
					CategoryThree: state.courseInfo.newCategory.three, // 
					Cpic: state.courseInfo.Cpic, // 
					ID: state.courseInfo.ID, //  id
					Cdetails: state.courseInfo.Cdetails, //	
				}
			}).then((res) => {
				if(res.data.success) {
					window.location.hash = "/course"
				} else {
					alert(res.data.err)
				}
			}).catch((err) => {
				alert(err)
			})
		}
	},
	//  
	alertSwitch: {
		addDir: false,
		selectVideo: false,
		uploadImg: false
	},
	// 
	courseInfo: {
		ID: "", // id
		getCourseInfoData: getCourseInfoData,
		Cname: "", //
		Cprice: "", // 
		Cdescribe: "", // 
		Cpic: "", // 
		category: { // 
			one: "",
			two: "",
			three: ""
		},
		newCategory: { // 
			one: "",
			two: "",
			three: ""
		},
		Cdetails: "" // 
	},
	// 
	courseContent: {
		setListData: setListData, // 
		dirTableList: { // 
			th_list: [{
					value: "",
					type: 'CDName',
					width: "20%"
				},
				{
					value: "",
					tyoe: 'CourseNumber',
					width: "50%"
				},
				{
					value: "",
					width: "30%"
				},
			],
			list: [],
			btn_list: { // 
				delete: function(data) { // 
					axios({
						url: "/VueHandler/CourseHandler?action=deleteDirectory",
						method: "get",
						params: {
							ID: data.ID // ID
						}
					}).then((res) => {
						if(res.data.success) {
							alert('');
							//  
							setListData('dirTableList', res.data.data);
							state.courseContent.videoTableList.list = [];
						} else {
							alert(res.data.err)
						}
					}).catch((err) => {
						alert(err)
					})
				},
				update: function(dirData) { // 
					Vue.prototype.$courseEditStore.commit('changeAlertSwitch', {
						type: "addDir",
						switch: true
					});
					Vue.prototype.$courseEditStore.commit('setDirAlertData', dirData);
				},
				getVideoList: function(data) { //  
					// state.courseContent.videoTableList.list = [];
					state.selectVideAlertData.ID = data.ID; // ID
					axios({
						url: "/VueHandler/VideoHandler?action=showdir",
						method: "get",
						params: {
							ID: data.ID // ID
						}
					}).then((res) => {
						if(res.data.success) {
							//  
							setListData('videoTableList', res.data.data, data.ID);

						} else {
							alert(res.data.err)
						}
					}).catch((err) => {
						alert(err)
					})
				}
			},
			hideFoot: true, //
			isClick: false // 
		},
		videoTableList: { // 
			th_list: [{
					value: "",
					type: 'Vname',
					width: "20%"
				},
				{
					value: "",
					type: 'Vtime',
					width: "60%"
				},
				{
					value: "",
					width: "20%"
				}
			],
			list: [],
			btn_list: { // 
				delete: function(data) { // 
					axios({
						url: "/VueHandler/CourseHandler?action=delvideo",
						method: "get",
						params: {
							Did: data.Did, // ID
							ID: data.ID // idid
						}
					}).then((res) => {
						if(res.data.success) {
							alert("");
							setListData('videoTableList', res.data.data.list, data.Did);
						} else {
							alert(res.data.err)
						}
					}).catch((err) => {
						alert(err)
					})
				}
			},
			hideFoot: true, //
		}
	}
}

const mutations = {
	clearCourseInfo: function(state) {
		state.courseInfo = {
			ID: "", // id
			getCourseInfoData: getCourseInfoData,
			Cname: "", //
			Cprice: "", // 
			Cdescribe: "", // 
			Cpic: "", // 
			category: { // 
				one: "",
				two: "",
				three: ""
			},
			newCategory: { // 
				one: "",
				two: "",
				three: ""
			},
			Cdetails: "" // 
		}
	},

	changeAddCourseSwitch: function(state, data) {
		state.addCourseSwitch = data;
	},
	changeAlertSwitch: function(state, data) {
		//  {type:"",switch:""}
		if(data.type == "selectVideo") {
			var list = state.courseContent.dirTableList.list;
			for(var i = 0, len = list.length; i < len; i++) {
				if(list[i].isClick == true) {
					state.alertSwitch[data.type] = data.switch;
					return false;
				}
			}
			alert('')
		} else {
			state.alertSwitch[data.type] = data.switch;
		}
	},
	setCategory: function(state, data) { // 
		state.courseInfo.newCategory = { // 
			one: data.categoryOne,
			two: data.categoryTwo,
			three: data.categoryThree
		}
	},
	setDirAlertData: function(state, dirData) {
		state.dirAlertData.Cid = state.courseInfo.ID; // ID
		if(dirData) {
			state.dirAlertData.CDName = dirData.CDName;
			state.dirAlertData.ID = dirData.ID;
		} else {
			state.dirAlertData.CDName = "";
			state.dirAlertData.ID = "";
		}
	}
}
export default new Vuex.Store({
	state,
	mutations
})


